/* EditUserModal.css */

.edit-user-modal {
  background: #fff;
  padding: 40px;
  border-radius: 8px;
  width: 71vh;
  max-width: 90%;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  gap: 16px;
  height: 40vh;
}

.edit-user-modal h2 {
  margin-bottom: 20px;
  text-align: center;
}

.edit-user-modal input,
.edit-user-modal select {
  width: 100%;
  padding: 13px 12px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 16px;
  box-sizing: border-box;
  margin-bottom: 15px;
}

.edit-user-modal-actions {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

.edit-user-modal-actions button {
  flex: 1;
  padding: 10px;
  font-size: 15px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.2s ease;
}

.edit-user-modal-actions-save-button {
  background-color: #1976d2;
  color: white;
}

.edit-user-modal-actions-edit-button {
  background-color: #2196f3;
  color: white;
  border: none;
  border-radius: 6px;
}

.edit-user-modal-actions-delete-button {
  background-color: #f44336;
  color: white;
  border: none;
  border-radius: 6px;
}

.edit-user-modal-actions-cancel-button {
  background-color: #9e9e9e;
  color: white;
}

.edit-user-modal-actions-save-button:hover,
.edit-user-modal-actions-edit-button:hover,
.edit-user-modal-actions-delete-button:hover,
.edit-user-modal-actions-cancel-button:hover {
  opacity: 0.9;
}

.edit-user-modal-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}
