/* EditFlightModal.css */

.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 999;
  }
  
  .edit-flight-modal {
    background: #fff;
    padding: 40px 60px;
    border-radius: 10px;
    width: 500px;
    max-height: 90vh;
    overflow-y: auto;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.2);
    height: 70vh;
  }
  
  .edit-flight-modal h2 {
    margin-bottom: 20px;
  }
  
  .edit-flight-modal input,
  .edit-flight-modal textarea {
    width: 100%;
    padding: 12px;
    margin-bottom: 16px;
    border: 1px solid #ccc;
    border-radius: 6px;
  }
  
  .edit-flight-modal textarea {
    resize: vertical;
    min-height: 80px;
  }
  
  .edit-flight-modal-actions {
    margin-top: 20px;
    display: flex;
    justify-content: flex-end;
    gap: 10px;
  }
  
  .cancel-button,
  .save-button {
    padding: 10px 20px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-weight: bold;
  }
  
  .cancel-button {
    background-color: #ccc;
    color: #333;
  }
  
  .save-button {
    background-color: #1976d2;
    color: white;
  }
  