/* Users.css */

.users-page {
    padding: 5px 30px;
    display: flex;
    flex-direction: column;
    height: 90vh;
  }
  
  h2 {
    margin-bottom: 20px;
  }
  
  .users-list-container {
    flex: 1;
    overflow-y: auto;
    border: 1px solid #ccc;
    padding: 5px;
    border-radius: 8px;
    background: #f9f9f9;
  }
  
  .users-list {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .user-item {
    padding: 10px 12px;
    border-bottom: 1px solid #ddd;
    background: #fff;
    margin-bottom: 5px;
    border-radius: 4px;
    cursor: pointer;
  }
  .user-item:hover{
    background: #d8d8d8;

  }
  
  .add-user-action {
    margin-top: 20px;
    display: flex;
    justify-content: flex-end;
  }
  
  .users-page-title-con {
    display: flex
    ;
        justify-content: space-between;
  }

  .backup-button {
    background-color: #000000;
    color: white;
    border: none;
    padding: 10px 16px;
    border-radius: 6px;
    cursor: pointer;
    margin: 14px;
  }

  .tooltip-container {
    position: relative;
    display: inline-block;
  }
  
  .tooltip-text {
    visibility: hidden;
    background-color: #444;
    color: #fff;
    text-align: left;
    padding: 8px;
    border-radius: 5px;
    position: absolute;
    z-index: 10;
    top: 40px;
    left: 0;
    white-space: nowrap;
  }
  
  .tooltip-container:hover .tooltip-text {
    visibility: visible;
  }
  