/* EditPassengerModal.css */

.edit-passenger-modal-2 {
  background: #fff;
  padding: 40px;
  border-radius: 8px;
  width: 70vh;
  max-width: 90%;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.edit-passenger-modal-2 h2 {
  margin-bottom: 20px;
  text-align: center;
}

.edit-passenger-modal-2 input,
.edit-passenger-modal-2 textarea {
  width: 100%;
  padding: 10px 12px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 16px;
}

.edit-passenger-modal-2 textarea {
  resize: vertical;
  min-height: 80px;
}

.modal-actions {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

.modal-actions button {
  flex: 1;
  padding: 10px;
  font-size: 15px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.2s ease;
}

.save-button {
  background-color: #1976d2;
  color: white;
}

.edit-button {
  background-color: #2196f3;
  color: white;
  border: none;
  border-radius: 6px;
}

.delete-button {
  background-color: #f44336;
  color: white;
  border: none;
  border-radius: 6px;
}

.cancel-button {
  background-color: #9e9e9e;
  color: white;
}

.save-button:hover,
.edit-button:hover,
.delete-button:hover,
.cancel-button:hover {
  opacity: 0.9;
}
