/* AddUserModal.css */

.add-user-modal {
  background: #fff;
  padding: 40px;
  border-radius: 10px;
  width: 400px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.2);
}

.modal-form {
  display: flex;
  flex-direction: column;
}
.modal-form input,
.modal-form select {
  margin-bottom: 15px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 6px;
}

.error-text {
  color: red;
  margin-bottom: 10px;
}

.success-text {
  color: green;
  margin-bottom: 10px;
}

.modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}