.sidebar {
  width: 20vh;
  height: 98vh;
  background: #0f172a;
  color: white;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: width 0.1s ease-in-out;
}

.sidebar.collapsed {
  width: 8vh;
}

.toggle-button {
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  align-self: flex-end;
}
.toggle-button-collapsed {
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  align-self: center;
}
.menu-list {
  flex: 1;
  list-style: none;
  padding: 0;
}

.menu-item {
  display: flex;
  align-items: center;
  gap: 20px;
  padding: 12px;
  padding-left: 0;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.5s ease;
}

.menu-item:hover {
  background: rgba(255, 255, 255, 0.1);
}

.icon {
  font-size: 14px;
}

.text {
  font-size: 12px;
}

.sidebar.collapsed .menu-item {
  justify-content: center;
  padding: 12px;
}

.sidebar.collapsed .text {
  display: none;
}

.sidebar.collapsed .logout-button {
  justify-content: center;
}

.sidebar.collapsed .logout-button span {
  display: none;
}
.header-sec {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 10px;
}

.header-sec-collapsed {
  padding: 0px;
}

.compName {
  font-size: 14px;
  align-items: center;
  font-weight: bold;
}

.compName-collapsed {
  display: none;
}

.user-name-text {
  text-align: start;
  color: darkgray;
  padding-top: 0px;
  font-size: 11px;
  text-transform: capitalize;
  margin-left: 15px;
}

.user-name-text-collapsed {
  display: none;
}
.compName-icon{
  width: 80%;
}