.edit-passenger-modal {
    background: #fff;
    padding: 40px 60px;
    border-radius: 8px;
    width: 500px;
    max-width: 90%;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.2);
    position: relative;
  }
  
  .edit-passenger-modal h2 {
    margin-bottom: 20px;
    text-align: center;
  }
  
  .edit-passenger-modal form {
    display: flex;
    flex-direction: column;
  }
  
  .edit-passenger-modal input,
  .edit-passenger-modal textarea,
  .edit-passenger-modal select {
    margin-bottom: 12px;
    padding: 10px 12px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 8px;
    box-sizing: border-box;
  }
  
  .edit-passenger-modal textarea {
    resize: vertical;
    min-height: 80px;
  }
  
  .modal-actions {
    display: flex;
    justify-content: flex-end;
    margin-top: 20px;
  }
  
  .modal-actions button {
    margin-left: 10px;
  }
  