/* Passengers.css */

.passengers-page-y {
  padding: 5px 30px;
  display: flex;
  flex-direction: column;
  height: 90vh;
}

.passengers-page-y h2 {
  margin-bottom: 20px;
}

.passengers-list-container-y {
  flex: 1;
  overflow-y: auto;
  border: 1px solid #ccc;
  padding: 5px;
  border-radius: 8px;
  background: #f9f9f9;
}

.passengers-list-y {
  list-style: none;
  padding: 0;
  margin: 0;
}

.passenger-item-y {
  padding: 10px 12px;
  border-bottom: 1px solid #ddd;
  background: #fff;
  margin-bottom: 5px;
  border-radius: 4px;
  cursor: pointer;
}
.passenger-item-y:hover {
  background: #d8d8d8;
}

.passengers-header-y {
  display: flex
  ;
      flex-direction: row;
      justify-content: space-between;
}


.passenger-search-input-y{
 margin: 15px;
    padding: 6px 10px;
    border-radius: 6px;
    border: 1px solid #ccc;
    font-size: 14px;
    width: 100%;
    max-width: 300px;
}