/* PrintSettingsModal.css */


  
  .print-passenger-detail-modal {
    background: #fff;
    padding: 24px;
    max-width: 70%;
    width: 70vh;
    border-radius: 10px;
    box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
    animation: fadeIn 0.3s ease-in-out;
    height: 60vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .print-passenger-detail-modal h2 {
    font-size: 20px;
    margin: 0 0 16px;
    text-align: center;
  }
  
  .print-passenger-scrollable-content {
    flex: 1;
    overflow-y: auto;
    padding-right: 4px;
  }
  
  .print-passenger-list-section {
    margin-bottom: 20px;
  }
  
  .print-passenger-checkbox {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
    gap: 8px;
    font-size: 15px;
  }
  .print-modal-buttons {
    display: flex;
    justify-content: space-between;
    gap: 12px;
  }
  
  .print-modal-buttons button {
    flex: 1;
    padding: 10px 15px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 6px;
    border: none;
    transition: all 0.2s;
  }

  .print-passenger-list-section {
    max-height: 200px;
    overflow-y: auto;
    margin-bottom: 20px;
  }
  
  .print-passenger-checkbox {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
    gap: 8px;
    font-size: 15px;
  }

  .print-settings-section {
    margin-top: 20px;
    display: flex;
    flex-direction: row;
    gap: 20%;
  }
  
  
  
