.add-ticket-details-modal {
  background: #fff;
  padding: 22px 37px;
  border-radius: 8px;
  max-width: 90%;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.2);
  height: 75vh;
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
  width: 75vh;
  flex-direction: column;
  justify-content: space-between;
}

.add-ticket-scroll-container {
  flex: 1;
  overflow-y: auto;
  padding: 10px;
  margin-bottom: 12px;
}

.add-ticket-passenger-card {
  border: 1px solid #8a8a8a;
  border-radius: 8px;
  padding: 15px;
  margin-bottom: 20px;
}

.add-ticket-passenger-card h3 {
  margin: 10px;
}

.add-ticket-details-modal input,
.add-ticket-details-modal select,
.add-ticket-details-modal textarea {
  margin-bottom: 15px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 6px;
  box-sizing: border-box;
  width: 100%;
}

.add-ticket-modal-actions {
  display: flex;
  width: 100%;
  gap: 6px;
  flex-wrap: wrap;
  justify-content: flex-end;
  padding-top: 10px;
  background-color: white;
}

.add-ticket-cancel-button,
.add-ticket-save-button,
.add-ticket-reservation-button {
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  cursor: pointer;
}

.add-ticket-cancel-button {
  background-color: #acacac;
}

.add-ticket-save-button,
.add-ticket-reservation-button {
  background-color: #1976d2;
  color: white;
}

.add-ticket-cancel-button:hover {
  background-color: #acacacbd;
}

.add-ticket-save-button:hover,
.add-ticket-reservation-button:hover {
  background-color: #115293;
}

/* TicketCount bileşeni için */

.add-ticket-count-modal {
  background: #fff;
  padding: 22px 37px;
  border-radius: 8px;
  max-width: 600px;
  width: 90%;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.2);
  height: 75vh;
  display: flex;
  flex-direction: column;
}


.add-ticket-age-selector {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 0;
  border-bottom: 1px solid #e0e0e0;
  font-family: sans-serif;
  margin-bottom: 10px;
  width: 100%;
}

.add-ticket-label span {
  font-weight: normal;
  color: #555;
  margin-left: 4px;
}

.add-ticket-counter {
  display: flex;
  align-items: center;
  gap: 10px;
}
.add-ticket-counter span {
 font-weight: bold;
}

.add-ticket-circle-button {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: 2px solid #1a2e5e;
  color: #1a2e5e;
  background: transparent;
  font-size: 20px;
  line-height: 1;
  text-align: center;
  cursor: pointer;
}
.flight-selection {
  margin-bottom: 15px;
}
.flight-selection label {
  display: block;
  margin: 8px 0 4px;
  font-weight: bold;
}
.flight-selection select,
.flight-selection input[type="date"] {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 6px;
  margin-bottom: 10px;
  box-sizing: border-box;
}

.add-ticket-content-scroll {
  flex: 1;
  overflow-y: auto;
  padding-right: 5px;
}

.flight-option-card {
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 12px;
  margin-bottom: 10px;
  cursor: pointer;
  transition: all 0.2s ease;
  background: #f9f9f9;
}
.flight-option-card:hover {
  background: #eef3ff;
}
.flight-option-card.selected {
  border: 2px solid #1976d2;
  background-color: #e3f2fd;
}

.reservation-note-modal {
  background-color: #fff;
  padding: 51px;
  width: 50vh;
  height: 26vh;
  border-radius: 10px;
}

.reservation-note-modal textarea {
  width: 436px;
    margin-bottom: 20px;
    height: 124px;
    padding: 10px;
    border-radius: 10px;
    max-height: 13vh;
    max-width: 48vh;
    min-width: 48vh;
    min-height: 13vh;
}