.select-passenger-modal {
  background: #fff;
  padding: 30px;
  border-radius: 10px;
  width: 350px;
  max-width: 90%;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  text-align: center;
}

.passenger-count-input {
  width: 100px;
  padding: 10px;
  margin: 20px 0;
  font-size: 18px;
  border: 1px solid #ccc;
  border-radius: 6px;
  text-align: center;
}

.error-p {
  color: red;
  padding: 0;
  margin: 0;
  font-size: small;
  height: 16px;
}
