.add-passengers-modal {
  background: #fff;
  border-radius: 8px;
  width: 85vh;
  max-width: 90%;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  height: 85vh;
  padding: 22px 37px;
}

.add-passengers-modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-top: 5px;
}

.modal-form {
  height: 80vh;
}

.add-passenger-scroll-container {
  flex: 1;
  overflow-y: auto;
  margin-bottom: 10px;
  padding: 10px;
  max-height: 75%;
}

.input-group {
  position: relative;
}

.suggestions-list {
  position: absolute;
  top: 45px; /* input'un hemen altı */
  width: 100%;
  background: white;
  border: 1px solid #ccc;
  border-radius: 4px;
  max-height: 150px;
  overflow-y: auto;
  z-index: 100;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
}

.suggestion-item {
  padding: 8px 12px;
  cursor: pointer;
}

.suggestion-item:hover {
  background: #f5f5f5;
}

.modal-form input,
.modal-form select,
.modal-form textarea {
  margin-bottom: 15px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 6px;
  box-sizing: border-box;
  width: 100%;
}
.passenger-scroll-container {
  max-height: 52vh;
  overflow-y: auto;
  padding: 10px;
}

.passenger-card {
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 15px;
  margin-bottom: 20px;
}
.passenger-card h3 {
  margin: 10px;
}
.input-wrapper {
  position: relative;
}
.reservation-button {
  background-color: chocolate;
  color: #fff;
}

.actions-container {
  background-color: #ebebeb;
  height: 16%;
  display: flex
;
  align-items: flex-end;
  flex-direction: column;
  justify-content: center;
  border-top: 1px solid #ddd;
  /* width: 100%; */
  border-radius: 10px;
  padding: 20px;
}
