.calendar-wrapper {
    padding: 20px;
    background: #fff;
    border-radius: 8px;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
  }
  
  .fc-toolbar-title {
    font-size: 22px;
    font-weight: bold;
  }
  
  .fc-daygrid-event {
    background-color: #4caf50;
    border: none;
    color: white;
    padding: 2px 4px;
    border-radius: 4px;
    font-size: 12px;
  }
  
  .fc-button-primary {
    background-color: #1976d2;
    border: none;
  }
  
  .fc-button-primary:hover {
    background-color: #115293;
  }
  .m-full-calendar{
    cursor: pointer;
  }

  .fc-event {
    cursor: pointer;
    transition: background-color 0.2s ease;
  }
  
  .fc-event:hover {
    background-color: #f0f0f0; /* Hoverda hafif grileşsin */
    opacity: 0.9;
  }
  
  .fc-customButton-button  {
    background-color: #4caf50 !important; /* Mavi */

  }
  
