.container-row {
    display: flex;
    flex-direction: row;
  }
  .container {
    overflow-y: scroll;
    max-height: 100vh;
    scrollbar-width: none;
    width: 100%;
    background-color: rgba(224, 224, 224, 0.48);
  }
  .nav-header-sec {
    background: #0f172a;
    color: #fff;
    position: sticky;
    top: 0;
    z-index: 10;
    padding: 13px;
  }
  .nav-header-sec-text {
    text-align: center;
    font-size: large;
    width: 90%;
  }
  