.add-flight-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.add-flight-modal {
  background: #fff;
  border-radius: 8px;
  width: 65vh;
  max-width: 90%;
  height: 75vh;
  display: flex;
  flex-direction: column;
  position: relative;
}

.add-flight-modal-form {
  padding: 40px;
  overflow-y: auto;
  flex: 1;
}

.add-flight-modal-form input,
.add-flight-modal-form textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 12px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 14px;
}

.add-flight-modal-form textarea {
  resize: vertical;
  min-height: 60px;
}

.add-flight-modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  padding: 15px 40px;
  border-top: 1px solid #eee;
  background: #fff;
  position: sticky;
  bottom: 0;
}

.add-flight-cancel-button {
  background-color: #ccc;
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  cursor: pointer;
}

.add-flight-save-button {
  background-color: #1976d2;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  cursor: pointer;
}

.add-flight-save-button:hover {
  background-color: #115293;
}

.add-flight-error-text {
  color: red;
  font-size: 14px;
  margin-bottom: 10px;
}


.add-flight-repeat-button {
  background-color: #eee;
  border: 1px solid #ccc;
  padding: 8px 12px;
  border-radius: 6px;
  cursor: pointer;
  width: 100%;
  margin-bottom: 12px;
}

.add-flight-repeat-section {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 12px;
}

.add-flight-repeat-select,
.add-flight-repeat-date {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 14px;
  width: 100%;
}
