.passenger-detail-modal {
  background: #fff;
  padding: 46px 83px;
  border-radius: 8px;
  width: 50%;
  height: auto;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.2);
}

.passenger-detail {
  margin: 20px 0;
}

.passenger-detail p {
  margin: 8px 0;
}

.passenger-actions {
  margin-top: 20px;
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}
.ticket-info {
  border: 1px solid #000;
  padding: 15px;
}
