/* PassengerListModal.css */
.passenger-list-modal {
  background: #fff;
  padding: 20px 30px;
  border-radius: 8px;
  width: 60%;
  height: 70%;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  position: relative;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.passenger-list-container {
  flex: 1;
  overflow-y: auto;
  margin: 20px 0;
  padding-right: 8px;
}

.passenger-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.passenger-item {
  padding: 8px 16px;
  border: 1px solid #ddd;
  margin-bottom: 5px;
  border-radius: 4px;
  background: #f9f9f9;
  cursor: pointer;
  transition: background 0.2s;
  width: 95%;
}

.passenger-item:hover {
  background: #eef1f6;
}

.modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-top: 20px;
}
.passenger-group {
  margin-bottom: -12px;
  padding-left: 0px;
}

.passenger-item {
  padding: 8px 10px;
  cursor: pointer;
  position: relative;
}

.passenger-content {
  padding-left: 10px;
}

.multi-group {
  position: relative;
  padding-left: 1px;
}



.multi-group .passenger-item {
  border-right: 2px solid black;
  margin-right: 20px;
  position: relative;
}

.multi-group .passenger-item::before {
  content: "";
  position: absolute;
  right: -11px;
  top: 50%;
  transform: translateY(-50%);
  width: 10px;
  height: 2px;
  background: #afafaf;
}


.passenger-item {
  padding: 8px 10px;
  cursor: pointer;
  position: relative;
  display: flex;
  align-items: center;
}

.passenger-line {
  position: absolute;
  right: -13px;
  width: 2px;
  background: #afafaf;
}

.multi-group .passenger-item:not(.last) .passenger-line {
  top: 47%;
  height: 125%;
}

