.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #f3f4f6;
}

.login-form {
  display: flex;
  flex-direction: column;
  gap: 16px;
  width: 300px;
}

.login-input {
  padding: 15px;
  font-size: 16px;
  border-radius: 10px;
}

.login-button {
  padding: 10px;
  background-color: #2563eb;
  color: white;
  border: none;
  cursor: pointer;
  font-size: 16px;
  padding: 15px;
  font-size: 16px;
  border-radius: 10px;
}

.login-card {
  padding: 155px;
  box-shadow: 1px 1px 10px 0px #c1c1c182;
  background-color: #fff;
  border-radius: 20px;
}
.error-text {
  color: red;
  font-size: 14px;
  margin-top: -10px;
  margin-bottom: 10px;
  text-align: center;
}
