@media print {
  .bilet-view-modal-overlay,
  .bilet-view-modal {
    all: unset;
  }

  .bilet-view-modal-content {
    padding: 0;
    margin: 0;
    box-shadow: none;
    width: 100%;
  }

  .bilet-view-button,
  .bilet-view-modal-actions {
    display: none !important;
  }

  body {
    -webkit-print-color-adjust: exact !important;
    print-color-adjust: exact !important;
  }
}


.print-modal-overlay {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 30px;
}

.print-modal {
  background: white;
  width: 600px;
  max-height: 90vh;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.print-modal h2 {
  padding: 16px;
  margin: 0;
  border-bottom: 1px solid #ddd;
}

.print-modal-content-scroll {
  padding: 16px;
  overflow-y: auto;
  flex: 1;
}

.print-modal-section {
  margin-bottom: 20px;
}

.print-modal-checkbox {
  display: block;
  margin-bottom: 8px;
}

.print-modal-actions {
  display: flex;
  justify-content: flex-end;
  padding: 12px 16px;
  border-top: 1px solid #ddd;
  background: #f9f9f9;
}

.print-modal-button {
  padding: 8px 16px;
  margin-left: 8px;
  font-weight: bold;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.print-modal-button.cancel {
  background-color: #ccc;
}

.print-modal-button.confirm {
  background-color: #4caf50;
  color: white;
}
