.passenger-flights-modal-2 {
  background: #fff;
  padding: 40px;
  border-radius: 8px;
  width: 40%;
  max-width: 90%;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  gap: 16px;
  height: 70%;
  justify-content: space-between;
}

/* Başlıklar */
.passenger-flights-modal-2 h3 {
  font-size: 18px;
  color: #333;
  margin: 0;
}

/* Uçuş listesi */
.passenger-flights-flight-list {
  overflow-y: auto;
  padding: 10px 0;
  display: flex;
  flex-direction: column;
  gap: 10px;
}


/* Uçuş kartı */
.passenger-flights-flight-card {
  padding: 10px 15px;
  background-color: #f1f1f1;
  border-radius: 8px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: background-color 0.2s;
}

.passenger-flights-flight-card:hover {
  background-color: #e0e0e0;
}

/* Modal */
.passenger-flights-flight-modal {
  margin-top: 20px;
  padding: 20px;
  background-color: #ffffff;
  border: 1px solid #ccc;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  width: 60vh;
}

.passenger-flights-flight-modal h4 {
  margin-bottom: 10px;
}

.passenger-flights-flight-modal h5 {
  margin-top: 15px;
  margin-bottom: 5px;
}

.passenger-flights-flight-modal ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.passenger-flights-flight-modal li {
  margin-bottom: 5px;
}

.passenger-flights-modal-actions {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

.passenger-flights-modal-actions button {
  flex: 1;
  padding: 10px;
  font-size: 15px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.2s ease;
  background-color: #1976d2;
  color: #fff;
}
.passenger-flights-modal-actions button:hover {
  background-color: #1565c0d0;
}

.passenger-flights-list {
  list-style: none;
  padding: 0;
  margin: 20px 0;
  max-height: 90%;
  overflow-y: auto;
  height: 80%;
}
